



































































































import {Vue, Component} from 'vue-property-decorator';
import {appApi} from '@/api/index';

@Component({
  name: 'AppContentList',
})
export default class extends Vue {
  private tableData: object[] = [];
  private loading: boolean = false;
  private region: string = '';
  private dialogVisible: boolean = false;
  private dialogTitle: string = '添加文章';
  private selectList: any[] = [
    {label: '美食', value: '1'},
    {label: '新闻', value: '2'},
    {label: '八卦', value: '3'},
    {label: '体育', value: '4'},
    {label: '音乐', value: '5'},
  ];
  private dialogForm = {
    title: '',
    name: '',
    content: '',
    tag: '',
    state: true,
  };

  get selectObj() {
    const obj: any = {};
    for (const v of this.selectList) {
      obj[v.value] = v.label;
    }
    return obj;
  }

  private mounted() {
    this.getPageData();
  }

  private getPageData() {
    this.loading = true;
    appApi.contentList().then((data: any) => {
      this.tableData = data.data.list;
      this.loading = false;
    });
  }

  private changePageHandler(n: number) {
    this.getPageData();
  }

  private editRowData(obj: any) {
    this.dialogTitle = '修改';
    this.dialogVisible = true;
    const {title, name, tag, state, content} = obj;
    this.dialogForm = {
      title,
      name,
      tag: String(tag),
      state: state === 1,
      content,
    };
  }

  private addItem() {
    this.dialogTitle = '添加文章';
    this.dialogVisible = true;
    this.dialogForm = {
      title: '',
      name: '',
      content: '',
      tag: '',
      state: true,
    };
  }
}
